.hidden {
  display: none !important;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  color: #000000e6;
  font-optical-sizing: auto;
  background-color: #ddd;
  font-family: YS Text, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
}

body {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.container {
  --unit: 4px;
  padding: calc(var(--unit) * 3) calc(var(--unit) * 4);
  background-color: #fff;
  border-radius: 24px;
  width: 100vw;
  max-width: 384px;
  height: 100vh;
  max-height: 384px;
  position: relative;
  overflow: hidden;
}

@media only screen and (width <= 182px) {
  .container {
    --unit: 2px;
  }
}

.container:hover .controls {
  opacity: 1;
}

.container .header {
  z-index: 3;
  padding: calc(var(--unit) * 4);
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.container .header-left {
  color: #fff;
  text-align: center;
}

.container .header-left h4 {
  font-size: calc(var(--unit) * 6);
}

.container .header-left a {
  color: #fff;
  font-size: calc(var(--unit) * 4);
}

.container .header-left a:hover {
  text-decoration: none;
}

.container .header .controls {
  justify-content: flex-end;
  align-items: center;
  gap: calc(var(--unit) * 4);
  transition: opacity .2s linear;
  display: flex;
}

.container .header .controls .control {
  appearance: none;
  cursor: pointer;
  font-size: calc(var(--unit) * 6);
  width: calc(var(--unit) * 12);
  height: calc(var(--unit) * 12);
  opacity: .7;
  background-color: #fff;
  border: none;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container .header .controls .control:hover {
  opacity: 1;
}

.container .timer-logo {
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  transition: height .5s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.container .timer-logo.colored {
  z-index: 2;
  background-image: url("yandex_logo_en.a418f46d.png");
}

.container .timer-logo.colorless {
  background-image: url("yandex_logo_en_grey.a418fcc9.png");
}

.container .footer {
  bottom: calc(var(--unit) * 8);
  text-align: center;
  z-index: 3;
  color: #fff;
  text-shadow: 0 0 1px #000;
  position: absolute;
  left: 0;
  right: 0;
}

.container .footer h4 {
  font-size: calc(var(--unit) * 6);
}
